import Vue from 'vue'
import axios from 'axios'
import { Message, Loading } from 'element-ui'
// import store from '@/store/index'
let loading

// ElementUI 的 loading 與 close 方法
function startLoading () {
  loading = Loading.service({
    lock: true,
    text: 'Loading...',
    background: 'rgba(0, 0, 0, 0.7)'
  })
}

function endLoading () {
  loading.close()
}

// 請求攔截 統一設置 header
axios.interceptors.request.use(config => {
  startLoading()
  config.headers.Authorization = 'Bearer ' + sessionStorage.getItem('access_token')
  return config
}, error => {
  return Promise.reject(error)
})

// 響應攔截 錯誤處理
axios.interceptors.response.use(response => {
  endLoading()
  return response
}, error => {
  // console.log(error)
  endLoading()
  const { status } = error?.response
  // console.log('錯誤訊息', status)
  const errInfo = { success: false, message: '' }
  if (status === 400) {
    errInfo.message = '400 客戶端格式錯誤'
  } else if (status === 403) {
    errInfo.message = '403 權限不足'
  } else if (status === 404) {
    errInfo.message = '404 請求失敗(找不到頁面或API路徑)'
  } else if (status === 405) {
    errInfo.message = '405 HTTP請求方法錯誤'
  } else if (status === 415) {
    errInfo.message = '415 不支援的媒體類型'
  } else if (status === 429) {
    errInfo.message = '429 刷新次數過多，請稍後再試！'
  } else if (status === 500) {
    errInfo.message = '500 伺服器異常'
  } else {
    errInfo.message = '失敗！服務器中斷'
  }
  Message.error(error.response.data)
  Vue.prototype.$message({
    type: 'error',
    message: errInfo.message
  })
  return errInfo
})

// —————————— API管理 ——————————
let config = {}
// —————————— 共用資料 ——————————
export function GetMyIntroduce () {
  config = {
    method: 'GET',
    url: 'https://mywebsite-api.onrender.com/get_My_introduce'
  }
  return axios(config).then(res => res)
}
